.breadcrumbs {
    margin-bottom: 12px;
    &__inner{
        color: #494949;
        font-size: 13px;
        line-height: normal;
        a:not(.defLink){
            color: currentColor;
            text-decoration: none;
            position: relative;
            display: inline;
            &:not(.arrowDown){
                transition: all .3s ease-out;
                a:hover{
                    transition: all .3s ease-out;
                }
            }
            span{
                display: inline;
            }
        }
        .crumb{
            position: relative;
            &:not(:last-of-type){
                &::after{
                    content: "â€¢";
                    font-size: 15px;
                    margin: 0 2px;
                    font-family: auto;
                }
            }
            &{
                display: inline;
            }
            div {
                display: inline;
            }
            
        }
        .arrowDown{
            padding-right: 0;
            &::after{
                position: static;
                vertical-align: middle;
                font-size: 4px;
            }
            &::before{
                top: 100%;
                left: 0;
                width: 100%;
                position: absolute;
                content: "";
                height: 20px;
                background-color: transparent;
            }
            &:hover{
                color: var(--Flamenco);
            }
        }
        .dropdown{
            font-size: 13px;
            width: 305px;
            top: calc(100% + 8px);
            left: -8px;
            li{
                padding: 6px 0;
            }
        }
    }

}
@media(max-width: 1024px){
    .breadcrumbs__inner .arrowDown .dropdown{
        left: auto;
    }
    .breadcrumbs__inner{
        position: relative;
        .arrowDown, .crumb{
            position: static;
        }
    }
}
@media(max-width: 575px){
    .breadcrumbs{
        margin: 5px 0 15px;
        &__inner{
            .dropdown{
                top: calc(100% + 4px);
                padding: 5px 14px;
                width: 235px;
            }
            .arrowDown{
                &::after{
                    right: 0;
                }
            }
        }
    }
}